<script>

export default {
  name: "Input",
  props: ['title', 'placeholder', 'error', 'value', 'onChange'],
  data: function() {
    return {
      inputValue: ''
    }
  },
  watch: {
    inputValue: function() {
      this.onChange && this.onChange(this.inputValue)
    }
  },
  created() {
    if (this.value){
      this.inputValue = this.value
    }
  }
}
</script>

