import axios from 'axios'
import Cookies from 'js-cookie'

const state = () => ({
  tasks: [
    {id: 1, title: 'task 1', completed: true},
    {id: 2, title: 'task 2', completed: false},
    {id: 3, title: 'task 3', completed: false},
    {id: 4, title: 'task 4', completed: false},
    {id: 5, title: 'task 5', completed: false},
  ],
})

const getters = {
  activeTasks: (state) => {
    return state.tasks.filter(task => !task.completed)
  },
  completedTasks: (state) => {
    return state.tasks.filter(task => task.completed)
  }
}

const actions = {
  // markTaskAsCompleted ({state, commit}, taskID) {
  //   axios
  //     .post(
  //       `http://127.0.0.1:8000/api/tasks/changeState`,
  //       {
  //         taskID: taskID
  //       },
  //       {
  //         headers: {
  //           'Content-Type'               : 'application/json',
  //           'Access-Control-Allow-Origin': '*',
  //         },
  //         // `xsrfCookieName` is the name of the cookie to use as a value for xsrf token
  //         xsrfCookieName: 'XSRF-TOKEN', // default
  //       }
  //     ).then((res)=>{ console.log(res) })
  //   commit('setTaskState', { task: state.tasks.filter(task => task.id === taskID)[0], taskState: true})
  // },
  changeTaskStateTo ({dispatch}, {taskID, taskNewState}) {
    axios
      .post(
        'http://127.0.0.1:8000/api/tasks/changeState',
        {
          taskID: taskID,
          state: taskNewState
        },
        {
          headers: {
            'Content-Type'               : 'application/json',
            'Access-Control-Allow-Origin': '*',
          },
          // `xsrfCookieName` is the name of the cookie to use as a value for xsrf token
          xsrfCookieName: 'XSRF-TOKEN', // default
        }
      ).then(() => dispatch('doGetAllTasks'))
      // commit('setTaskState', { task: state.tasks.filter(task => task.id === taskID)[0], taskState: true})
  },
  changeTaskCredentials ({dispatch}, {taskID, title, description, responseHandler}) {
    axios
      .post(
        'http://127.0.0.1:8000/api/tasks/changeCredentials',
        {
          taskID: taskID,
          title: title,
          description: description
        },
        {
          headers: {
            'Content-Type'               : 'application/json',
            'Access-Control-Allow-Origin': '*',
          },
          // `xsrfCookieName` is the name of the cookie to use as a value for xsrf token
          xsrfCookieName: 'XSRF-TOKEN', // default
        }
      ).then((res) => {
      responseHandler(res)
      dispatch('doGetAllTasks')
    })
  },
  showActiveTasks ({state}) {
    console.log(state.tasks.filter(task => !task.completed))
  },
  doGetAllTasks ({commit}) {
    axios
      .get(`http://127.0.0.1:8000/api/tasks/list`)
      .then(res => {
        // console.log(Object.values(res.data.tasks))
        commit('setTasks', { tasksArray: Object.values(res.data.tasks) })
      })
  },
  doAddTask ({state}, payload) {
    console.log(state)
    // console.log(`title: ${payload.title}`)
    // console.log(`descr: ${payload.description}`)
    axios
      .post(
        `http://127.0.0.1:8000/api/tasks/add?name=${payload.title}&description=${payload.description}`,
        // {},
        {
          name: payload.title,
          description: payload.description
        },
        {
          headers: {
            'Content-Type'               : 'application/json',
            'Access-Control-Allow-Origin': '*',
          },
          // `xsrfCookieName` is the name of the cookie to use as a value for xsrf token
          xsrfCookieName: 'XSRF-TOKEN', // default
        }
      ).then(payload.responseHandler)
  },
  login ({state}, {username, password}) {
    console.log(state)
    console.log(username)
    console.log(password)
    Cookies.set('csrftoket', 'sjdhf')
    axios
      .post(
        'http://127.0.0.1:8000/api/user/login',
        {
          u: username,
          p: password
        },
        {
          headers: {
            'X-CSRFToken'                : Cookies.get('csrftoket'),
            'Content-Type'               : 'application/json',
            // 'Access-Control-Allow-Origin': '*',
          },
          // `xsrfCookieName` is the name of the cookie to use as a value for xsrf token
          // xsrfCookieName: 'XSRF-TOKEN', // default
        }
      ).then(res => console.log(res))
  },
  userInfo () {
    Cookies.set('csrftoket', 'sjdhf')
    axios
      .get(
        'http://127.0.0.1:8000/api/user/info',
        {
          withCredentials: true,
          headers: {
            'X-CSRFToken'                : Cookies.get('csrftoket'),
            'Content-Type'               : 'application/json',
            'Access-Control-Allow-Origin': '*',
          },
          // `xsrfCookieName` is the name of the cookie to use as a value for xsrf token
          // xsrfCookieName: 'XSRF-TOKEN', // default
        }
      ).then(res => console.log(res))
  }
}

const mutations = {
  setTaskState (state, {task, taskState}) {
    state.tasks.map(item => {
      if (item.id === task.id){
        item.completed = taskState
      }
      return item
    })
  },
  setTasks (state, {tasksArray}) {
    state.tasks = tasksArray
  }
}


export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}