<template>
  <!--  <div class="rounded-2xl items-center justify-center">-->
  <!--    <button-->
  <!--        type="button"-->
  <!--        @click="openModal"-->
  <!--        class="px-4 py-2 text-sm font-medium text-white bg-black rounded-md bg-opacity-20 hover:bg-opacity-30 focus:outline-none focus-visible:ring-2 focus-visible:ring-white focus-visible:ring-opacity-75"-->
  <!--    >-->
  <!--      Open dialog-->
  <!--    </button>-->
  <!--  </div>-->
  <TransitionRoot :show="toggleModal" as="template">
    <Dialog as="div" @close="closeModal">
      <div class="fixed inset-0 z-10 overflow-y-auto">
        <div class="min-h-screen px-4 text-center">
          <TransitionChild
              as="template"
              enter="duration-300 ease-out"
              enter-from="opacity-0"
              enter-to="opacity-100"
              leave="duration-200 ease-in"
              leave-from="opacity-100"
              leave-to="opacity-0"
          >
            <DialogOverlay class="fixed inset-0"/>
          </TransitionChild>

          <span class="inline-block h-screen align-middle" aria-hidden="true">
            &#8203;
          </span>

          <TransitionChild
              as="template"
              enter="duration-300 ease-out"
              enter-from="opacity-0 scale-95"
              enter-to="opacity-100 scale-100"
              leave="duration-200 ease-in"
              leave-from="opacity-100 scale-100"
              leave-to="opacity-0 scale-95"
          >
            <div
                class="inline-block w-full max-w-md p-6 my-8 overflow-hidden text-left align-middle transition-all transform bg-white shadow-xl rounded-2xl"
            >
              <DialogTitle
                  as="h3"
                  class="text-2xl font-medium leading-6 text-gray-900"
              >
                Add new task
              </DialogTitle>
              <div class="mt-2">
                <!--                <p class="text-sm text-gray-500">-->
                <!--                  Your payment has been successfully submitted. Weâ€™ve sent you-->
                <!--                  an email with all of the details of your order.-->
                <!--                </p>-->
                <Input :on-change="onTitleChange" error="false" title="Title" placeholder="simple placeholder"/>
                <Input :on-change="onDescriptionChange" error="false" title="Description"
                       placeholder="simple placeholder"/>
                <Input :on-change="onTimeChange" error="false" title="Time" placeholder="simple placeholder"/>
              </div>

              <div class="mt-4">
                <button
                    type="button"
                    class="inline-flex justify-center px-4 py-2 text-sm font-medium text-blue-900 bg-blue-100 border border-transparent rounded-md hover:bg-blue-200 focus:outline-none focus-visible:ring-2 focus-visible:ring-offset-2 focus-visible:ring-blue-500"
                    @click="addTask"
                >
                  Add
                </button>
                <button
                    type="button"
                    class="inline-flex justify-center px-4 py-2 ml-4 text-sm font-medium text-blue-900 bg-red-100 border border-transparent rounded-md hover:bg-red-200 focus:outline-none focus-visible:ring-2 focus-visible:ring-offset-2 focus-visible:ring-blue-500"
                    @click="closeModal"
                >
                  Close
                </button>
              </div>
            </div>
          </TransitionChild>
        </div>
      </div>
    </Dialog>
  </TransitionRoot>
</template>

<script>
// import { ref } from 'vue'
import {
  TransitionRoot,
  TransitionChild,
  Dialog,
  DialogOverlay,
  DialogTitle,
} from '@headlessui/vue'

import Input from '../Input'
import {mapActions} from "vuex";

export default {
  components: {
    TransitionRoot,
    TransitionChild,
    Dialog,
    DialogOverlay,
    DialogTitle,
    Input
  },
  data: () => {
    return {
      titleValue: '',
      descriptionValue: '',
      timeValue: ''
    }
  },
  props: ['toggleModal', 'closeModal'],
  methods: {
    onTitleChange: function (title) {
      this.titleValue = title
    },
    onDescriptionChange: function (description) {
      this.descriptionValue = description
    },
    onTimeChange: function (time) {
      this.timeValue = time
    },
    showProps: function () {
      console.log(`title: ${this.titleValue}`)
      console.log(`descr: ${this.descriptionValue}`)
      console.log(`time : ${this.timeValue}`)
    },
    addTask: function () {
      this.doAddTask({
            title: this.titleValue,
            description: this.descriptionValue,
            responseHandler: (res) => {
              if (res.data.status === 'task-added'){
                this.closeModal && this.closeModal()
                this.doGetAllTasks && this.doGetAllTasks()
              }
            }
          }
      )
    },
    ...mapActions('tasks', [
      'doAddTask',
      'doGetAllTasks'
    ])
  }
}
</script>
