import Vuex from 'vuex'
import tasks from './modules/tasks.js'

const debug = process.env.NODE_ENV !== 'production'

export default Vuex.createStore({
  modules: {
    tasks
  },
  strict: debug,
  plugins: [],
  state: {
    test: 0
  },
  mutations: {
    addToTest (state) {
      state.test++
    }
  }
});